import React, { Component } from 'react';

//CSS
import './main.css';

export default class About extends Component {
  render() {
    return (
      <section className="shompi_section">
        <div className="shompi_section_content">       
          <div className="shompi_main_title">
            <span>Information</span>
            <h3>About Me</h3>
          </div>
          <div className="texts mt-10px">
            <p>My name is <strong>Felipe Schneeberger</strong> and I am a creative person from Temuco, Chile. 
              My main professional focuses are: Backend development, and Artificial Intelligence. 
            </p>
            <p>I have skills to coordinate teams and I like being able to be a real contribution within 
              these, besides this I can work in multiple tasks in an organized way and respecting the 
              deadlines.
            </p>
            <p>I have a great versatility adaptable to the different programming languages used in the labor market, 
              although I have a specialization in the following technologies: <br/>  
              <strong> 
                <i class="ml-5px devicon-python-plain colored"></i> Python,  
                <i class="ml-5px devicon-nodejs-plain colored"></i> NodeJS,  
                <i class="ml-5px devicon-go-plain colored"></i> Go (Golang), 
                <i class="ml-5px devicon-mongodb-plain colored"></i> MongoDB, 
                <i class="ml-5px devicon-mysql-plain colored"></i> MariaDB - MySQL,  
                <i class="ml-5px devicon-postgresql-plain colored"></i> PostgreSQL.</strong>
            </p>
            <p>In my free time I dedicate myself to learning new methods of machine learning. 
            </p>
          </div>
        </div>
      </section>
    );
  }
}
