function Video(element) {
  console.log(element);
  return (
    <div>
    <img src={element.element.image} className="img-thumbnail" />
    <video
      srl_video_thumbnail={element.element.image} 
      srl_video_caption={element.element.caption} 
      srl_video_controls="true"
      srl_video_autoplay="true"
      srl_video_muted="true"
      style={{ display: 'none' }}
    >
      <source src={element.element.media}  type="video/mp4"/>
      Your browser does not support the video tag.
    </video>
    </div>
  );
}
export default Video;